"use client"
import { useState } from "react"

interface AllocationCellProps {
  userId: string
  monthIndex: number
  allocations?: any[]
  onEdit?: (id: string) => void
  onSaveEdit?: (id: string, percentage: number) => void
  onRemove?: (id: string) => void
  onEmptyCellClick?: () => void
  project?: any
  projects?: any[]
  month?: any
}

export function AllocationCell({
  userId,
  monthIndex,
  allocations = [],
  onEdit,
  onSaveEdit,
  onRemove,
  onEmptyCellClick,
  projects = [],
}: AllocationCellProps) {
  const [isHovering, setIsHovering] = useState(false)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [editValue, setEditValue] = useState(0)

  const totalAllocated = allocations.reduce((sum, a) => sum + (a.percentage || 0), 0)
  const freePercentage = Math.max(0, 100 - totalAllocated)

  const handleClick = () => {
    if (freePercentage > 0 && onEmptyCellClick) {
      onEmptyCellClick()
    }
  }

  return (
    <td
      data-user-id={userId}
      data-month={monthIndex}
      className={`w-32 border-r border-b border-border bg-background px-1 py-1 min-h-12 transition-colors relative ${
        freePercentage > 0 && onEmptyCellClick ? "cursor-pointer hover:bg-muted/50" : "hover:bg-muted/30"
      }`}
      onMouseEnter={() => freePercentage > 0 && setIsHovering(true)}
      onMouseLeave={() => setIsHovering(false)}
      onClick={handleClick}
    >
      <div className="flex flex-col h-full gap-1">
        {/* Allocation bars row */}
        <div className="flex items-center flex-1">
          <div className="flex w-full h-5 rounded overflow-hidden bg-gray-100">
            {allocations.map((allocation) => {
              const project = projects.find((p) => p.id === allocation.projectId)
              // Keep total bar size constant: if allocations exceed 100%,
              // normalize segments so they still fit into a 100% width bar.
              const capacity = totalAllocated > 100 ? totalAllocated : 100
              const width = Math.max(
                0,
                Math.min(100, ((allocation.percentage || 0) / capacity) * 100),
              )

              return (
                <div
                  key={allocation.id}
                  className="h-full text-[9px] flex items-center justify-center font-semibold relative group cursor-pointer text-white"
                  style={{
                    backgroundColor: project?.color || "#999",
                    width: `${width}%`,
                    minWidth: width > 0 ? "8%" : undefined,
                  }}
                  onClick={(e) => {
                    e.stopPropagation()
                    onEdit?.(allocation.id)
                  }}
                  title={`${project?.name ?? "Project"} - ${allocation.percentage || 0}%`}
                >
                  {allocation.percentage || 0}%
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      onRemove?.(allocation.id)
                    }}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs opacity-0 group-hover:opacity-100 transition-opacity"
                  >
                    ×
                  </button>
                </div>
              )
            })}
          </div>
        </div>

        {/* Availability / over-allocation text */}
        <div className="flex justify-end">
          {totalAllocated <= 100 ? (
            <span className="text-[10px] text-gray-500 whitespace-nowrap">{freePercentage}% free</span>
          ) : (
            <span className="text-[10px] text-red-600 font-medium whitespace-nowrap">
              {totalAllocated}% over
            </span>
          )}
        </div>

        {isHovering && freePercentage > 0 && (
          <div className="absolute inset-0 flex items-center justify-center bg-primary/10 rounded text-[10px] text-primary font-medium pointer-events-none">
            Click to select position
          </div>
        )}
      </div>
    </td>
  )
}
